:root {
  --bg-rgb: 0, 0, 0;
}

body {
  font-family: sans-serif;
  color: #41403c;
  box-sizing: border-box;
}

.wrapper {
  width: calc(100% - 2rem);
  margin: 0 auto;
  padding: 1rem;
}
@media screen and (min-width: 768px) {
  .wrapper {
    width: 70%;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  color: #d08856;
}

h1 {
  color: #aa210f;
}
h1 code {
  color: #2F5496;
}

pre {
  padding: 1rem;
  background-color: #dee2e6;
  border-radius: 0.25rem;
  color: #2F5496;
  border: 1px solid #dee2e6;
}

pre,
code {
  font-size: 1.2em;
}

a {
  color: #2F5496;
}

nav {
  margin-bottom: 1rem;
}
nav a {
  color: #aa210f;
}

table {
  width: 100%;
  border-color: #dee2e6;
  border-collapse: collapse;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border-color: inherit;
  border-style: solid;
  border-width: 0;
  vertical-align: bottom;
}

th,
td {
  padding: 0.5rem;
  border-bottom-width: 1px;
}

.success {
  color: #419941;
}

.danger {
  color: #aa210f;
}

.active {
  color: #2F5496;
}

.text-right {
  text-align: right;
}

form > p {
  margin-bottom: 1rem;
}

label {
  margin-bottom: 0.5rem;
  display: inline-block;
}

input,
textarea {
  display: block;
  width: 75%;
  padding: 0.375rem 0.75rem;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
}
@media screen and (min-width: 768px) {
  input,
  textarea {
    width: 25%;
  }
}

select {
  display: block;
  width: 75%;
  padding: 0.5625rem 2.25rem 0.5625rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #41403c;
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
@media screen and (min-width: 768px) {
  select {
    width: 25%;
  }
}

button,
input[type=submit],
input[type=reset] {
  width: auto;
  display: inline-block;
  padding: 0.375rem 0.75rem;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  background-color: rgba(var(--bg-rgb), 0.05);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
button:hover,
input[type=submit]:hover,
input[type=reset]:hover {
  background-color: rgba(var(--bg-rgb), 0.1);
}

input[type=checkbox],
input[type=radio] {
  display: inline;
  width: 1em;
  height: 1em;
  margin-top: 0.25em;
  border: 1px solid #dee2e6 !important;
}

.icon {
  text-decoration: none;
  cursor: pointer;
}
.icon svg {
  width: 24px;
  height: auto;
}

#pu_del {
  position: absolute;
  width: 329px;
  left: calc(50% - 164.5px);
  top: 100px;
  background-color: #edefee;
  border: 1px solid #dee2e6;
  border-radius: 0.375rem;
  padding: 1rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  transition: opacity 300ms ease-in-out;
}